// ==================== Hero ==================== BxSlider ====================

.bx-wrapper {
  border: none; }

.bx-wrapper .bx-pager,
.bx-wrapper .bx-controls-auto {
  bottom: 1rem; }

.bx-wrapper .bx-pager.bx-default-pager a:hover,
.bx-wrapper .bx-pager.bx-default-pager a.active,
.bx-wrapper .bx-pager.bx-default-pager a:focus {
  background: #eee; }


// ==================== Hero ==================== ====================
.hero {
  height: 39rem;
  position: relative;

  // 813px
  @media (min-width: 50.8125em) {
    margin-top: 5rem;
    height: 71rem; }

  &__slide {
    height: 39rem;
    width: 100%;

    // 813px
    @media (min-width: 50.8125em) {
      height: 71rem; }

    &--one {
      background: {
        color: #090909;
        image: url("../img/hero-bg.jpg");
        size: cover;
        position: 70% center;
        repeat: no-repeat; } }

    &--two {
      background: {
        color: #090909;
        image: url("../img/hero-bg-2.jpg");
        size: cover;
        position: 80% center;
        repeat: no-repeat; } } }

  &__text {
    position: absolute;
    top: 65%;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    align-items: center; }

  &__title {
    text-align: center;
    font-size: 2.2rem;
    text-transform: uppercase;
    color: #eee;
    margin-bottom: 6rem;

    // 376px
    @media (min-width: 23.5em) {
      font-size: 2.5rem; }

    // 813px
    @media (min-width: 50.8125em) {
      margin-bottom: 4.5rem; } }

  &__arrow {
    display: none;

    // 813px
    @media (min-width: 50.8125em) {
      display: block;
      margin-bottom: 11rem; }

    &:focus {
      // I subtracted combained thicknes of the outline (.6rem) from margin-bottom (11rem)
      margin-bottom: calc(11rem - .6rem);
      @include outline; }


    &-svg {
      animation: pulse 3s ease-in-out infinite; }

    @keyframes pulse {
      0%, 100% {
        transform: translateY(-15%); }
      50% {
        transform: translateY(15%); } } } }
